export default {
    data() {
        return {
            money: [1,5,10],  // 打赏金额
            suggest: "",  // 建议/补充
            num: 4,   // 默认选择打赏金额
            show: false   // 点赞之后弹窗
        }
    },
    methods:{
        good(){
            console.log("我给你点赞")
            this.show = true;
        },
        chaping(){
            console.log("我给你差评")
            this.show = true;
        },
        moneys(item,index){
            console.log("我要打赏",item)
            this.num = index;
        },
        submit(){
            this.$vux.toast.text('提交成功', 'middle')
        }
    }
}



